<template>
  <div class="wrapper">
    <div class="search1">
      <div
        class="search1__back iconfont"
        @click="handleBackClick"
      >&#xe604;</div>
       <div class="search1__content">
         <span class="search1__content__icon iconfont">&#xe602;</span>
         <input
          class="search1__content__input"
          placeholder="请输入商品名称"
        />
       </div>
    </div>
    <ShopInfo :item="item" :hideBorder="true" v-if="item.imgUrl"/>
    <Content/>
    <Cart/>
  </div>
</template>

<script>
// 获取 ShopInfo组件
import ShopInfo from '../../components/ShopInfo'
// 获取 content组件
import Content from './Content'
// 获取购物车 cart 组件
import Cart from './Cart'
import { useRouter, useRoute } from 'vue-router'
import { reactive, toRefs } from 'vue'
import { get } from '../../utils/request'
// 获取当前店铺
const useShopInfoEffect = () => {
  const route = useRoute()
  // 使用vue中reactive实现党数据变化时 页面会同时更新
  const data = reactive({ item: {} })
  const getItemData = async () => {
    const result = await get(`/api/shop/${route.params.id}`)
    if (result.errno === 0 && result.data) {
      data.item = result.data
    }
    console.log(result, '------------')
  }
  const { item } = toRefs(data)
  return { item, getItemData }
}
// 点击shop 页面最上面一行 < 时返回首页
const useShopBackEffect = () => {
  const router = useRouter()
  const handleBackClick = () => {
    router.back()
  }
  return handleBackClick
}
export default {
  name: 'Shop',
  components: { ShopInfo, Content, Cart },
  setup () {
    // 获取当前店铺
    const { item, getItemData } = useShopInfoEffect()
    // console.log(item)
    getItemData()
    // 点击shop 页面最上面一行 < 时返回首页
    const handleBackClick = useShopBackEffect()
    return { item, handleBackClick }
  }
}
</script>

